import React, { Component } from 'react'
import withStyles from '@material-ui/core/styles/withStyles';
import {Link} from 'react-router-dom';
import relativeTime from 'dayjs/plugin/relativeTime';
import dayjs from 'dayjs';
import PropTypes from 'prop-types';
import MyButton from '../../utils/myButton';
import DeleteScream from './DeleteScream';
import ScreamDialog from './ScreamDialog';
import LikeButton from './LikeButton';

// Material UI
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';

// Material UI Icon
import ChatIcon from '@material-ui/icons/Chat';

// Redux
import {connect} from 'react-redux';

const styles = {
    card:{
        position:'relative',
        display:'flex',
        marginBottom:20,
    },
    image:{
        minWidth:200,
    },
    content:{
        padding:25,
        objectFit:'cover',
    }
}

class Scream extends Component {
    render() {

        dayjs.extend(relativeTime);

        const { classes , 
                scream: {
                    body,createdAt,
                    userHandle,userImage,
                    screamId,likeCount,commentCount},
                user:{
                    authenticated,
                    credentials:{ handle }
                }
                } = this.props;

        const deleteButton = authenticated && userHandle === handle ? 
            <DeleteScream screamId={screamId}/>
        :null;

        return (
        <Card className = {classes.card}>
            <CardMedia 
            image={userImage}
            title="Profile Picture"
            className={classes.image}/>
            <CardContent className={classes.content}>
            <Typography 
            variant='h5' 
            component={Link} 
            to={`/users/${userHandle}`} 
            color="primary"
            >{userHandle}</Typography>
            {deleteButton}
            <Typography 
            variant='body2' 
            color='textSecondary'>
            {dayjs(createdAt).fromNow()}</Typography>
            <Typography 
            variant='body1'>
            {body}</Typography>
            <LikeButton screamId={screamId}/>
            <span>{likeCount} Likes</span>
            <MyButton tip="Comments"><ChatIcon color="primary"/></MyButton>
            <span>{commentCount} Comments</span>
            <ScreamDialog screamId={screamId} userHandle={userHandle} openDialog={this.props.openDialog}/>
            </CardContent>
        </Card> 
        )
    }
};

Scream.propTypes = {
    user:PropTypes.object.isRequired,
    scream:PropTypes.object.isRequired,
    classes:PropTypes.object.isRequired,
    openDialog:PropTypes.bool,
};

const mapStateToProps = (state) => {
    return{
        user:state.user,
    };
};

export default connect(mapStateToProps,null)(withStyles(styles)(Scream));